import React, { useEffect, useState } from "react";
import axios from "axios";
import Navbar from "../../components/navbar/navbar";
import "./dashboard.scoped.css";
import moments from "moment/moment";
import { useForm } from "react-hook-form";
import * as yup from "yup";
import { ToastContainer, toast } from "react-toastify";
// import Modal from "react-responsive-modal";
import Swal from "sweetalert2";
import "react-toastify/dist/ReactToastify.css";

// const rncrypt = require("./rncrpt");
// const API = "http://localhost:8080/api";

const API = "https://gonotesrizky.herokuapp.com/api";

// const jwt = require("jsonwebtoken");

// Make sure to bind modal to your appElement (http://reactcommunity.org/react-modal/accessibility/)
let timer = null;
const Dashboard = (props) => {
  const [todos, settodos] = useState([]);
  // const [username, setusername] = useState("");
  // const [newtodocontent, setnewtodocontent] = useState("");
  // const [id, setid] = useState("");
  const [isloading, setisloading] = useState(true);
  const [modelopen, setmodelopen] = useState(false);

  const [username, setusername] = useState("");
  const [selectednotecontent, setselectednotecontent] = useState("");
  const [selectednotetitle, setselectednotetitle] = useState("");
  const [selectednoteid, setselectednoteid] = useState("");
  const [selectednotecreated, setselectednotecreated] = useState("");
  const [selectednoteupdated, setselectednoteupdated] = useState("");
  useEffect(() => {
    var token = localStorage.getItem("jwt");

    if (!token) {
      props.history.push("/");
    }

    var config = {
      headers: { authorization: token },
    };
    axios
      .get(`${API}/getall`, config)
      .then((result) => {
        setisloading(false);
        console.log(result.data);
        // setid(result.data.id);
        settodos(result.data.notes);
        setusername(result.data.username);
        // setusername(result.data.name);
      })
      .catch((err) => {
        setisloading(false);
        console.log(err);
      });
  }, []);

  const getalltodos = () => {
    var token = localStorage.getItem("jwt");

    var config = {
      headers: { authorization: token },
    };
    setisloading(true);

    axios
      .get(`${API}/getall`, config)
      .then((result) => {
        // var todos = [];
        console.log(result.data);

        settodos(result.data.notes);
        setusername(result.data.username);
        setisloading(false);
      })
      .catch((err) => {
        setisloading(false);
        console.log(err);
      });
  };

  // var tx =   .getElementById("textareanewnote");
  // var tx = document.querySelector('#textareanewnote')
  var tx = document.getElementsByName("todo");
  // for (var i = 0; i < tx.length; i++) {
  //   tx[i].setAttribute(
  //     "style",
  //     "height:" + tx[i].scrollHeight + "px;overflow-y:hidden;"
  //   );
  //   tx[i].addEventListener("input", OnInput, false);
  // }

  if (tx[0]) {
    // console.log(tx);

    tx[0].setAttribute(
      "style",
      "height:" + tx.scrollHeight + "px;overflow-y:hidden;"
    );

    //   // tx.addEventListener()

    tx[0].addEventListener("input", OnInput, false);

    function OnInput() {
      this.style.height = "auto";
      this.style.height = this.scrollHeight + "px";
    }
  }

  const validation = yup.object().shape({
    todo: yup.string().required(),
  });
  const {
    formState: { isSubmitting },
    handleSubmit,
    register,
    reset,
    errors,
  } = useForm({ mode: "onChange", validationSchema: validation });

  const addTodo = (data) => {
    console.log("adding new todo..........");
    var token = localStorage.getItem("jwt");

    Swal.fire({
      title: "Enter note title",
      input: "text",
      inputAttributes: {
        autocapitalize: "off",
      },
      showCancelButton: true,
      confirmButtonText: "Add",
      showLoaderOnConfirm: true,
      preConfirm: (login) => {},
      allowOutsideClick: () => !Swal.isLoading(),
    }).then((result) => {
      console.log(result);
      console.log(data);

      var payload = {
        title: result.value,
        note: data.todo,
      };

      var config = {
        headers: { authorization: token },
      };
      setisloading(true);
      console.log(data);
      axios
        .post(`${API}/add`, payload, config)
        .then((result) => {
          console.log(result.data);

          getalltodos();
          setisloading(false);
          reset();

          toast.success("Note added successfully", {
            position: "bottom-left",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: false,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
        })
        .catch((err) => {
          setisloading(false);
          console.log(err);
        });
    });
  };

  const openModel = (noteid, title, content, created, updated) => {
    setmodelopen(true);
    setselectednotetitle(title);
    setselectednotecontent(content);
    setselectednoteid(noteid);
    setselectednotecreated(created);
    setselectednoteupdated(updated);
    // console.log("opne model");
  };
  const closeModel = () => {
    setmodelopen(false);
    setselectednotetitle("");
    setselectednotecontent("");
    setselectednotecreated("");
    setselectednoteupdated("");
    setselectednoteid("");
  };

  const editnote = (newtitle, newcontent) => {
    console.log(newtitle, newcontent);
    setselectednotetitle(newtitle);
    setselectednotecontent(newcontent);

    console.log(todos[0]);

    settodos((pre) => {
      return pre.map((ele) => {
        if (ele.id === selectednoteid) {
          ele.title = newtitle;
          ele.note = newcontent;
          console.log("foundddddddd");
          return ele;
        } else {
          return ele;
        }
      });
      // return pre.map(ele=>{if(ele._id===selectednoteid){ ele.title=newtitle ele.content=newcontent }:ele})
    });

    handleCheck(selectednoteid, { title: newtitle, content: newcontent });
  };

  const handleCheck = (id, newdata) => {
    // Clears running timer and starts a new one each time the user types
    clearTimeout(timer);
    timer = setTimeout(() => {
      // this.toggleCheck();
      delayedSave(id, newdata);
      console.log("fire fire fire");
    }, 1000);
  };

  const delayedSave = (id, newdata) => {
    var selnoteid = selectednoteid;

    settodos((pre) => {
      var noteindex = pre
        .map(function (e) {
          return e.id;
        })
        .indexOf(selnoteid);
      console.log(noteindex);
      var newmodifiiedobj = pre.splice(noteindex, 1);
      newmodifiiedobj[0].updated = new Date();

      return [...newmodifiiedobj, ...pre];
    });

    var token = localStorage.getItem("jwt");

    var config = {
      headers: { authorization: token },
    };
    setisloading(true);

    var payload = {
      noteid: id,
      note: newdata.content,
      title: newdata.title,
    };

    console.log(payload);

    axios
      .post(`${API}/update`, payload, config)
      .then((result) => {
        // var todos = [];
        console.log(result.data);

        toast.success("Note edited successfully", {
          position: "bottom-left",
          autoClose: 1000,
          hideProgressBar: false,
          closeOnClick: false,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });

        setisloading(false);
      })
      .catch((err) => {
        setisloading(false);
        console.log(err);
        toast.error("Error", {
          position: "bottom-left",
          autoClose: 1000,
          hideProgressBar: false,
          closeOnClick: false,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      });
  };

  const deactivatenoe = () => {
    Swal.fire({
      title: "Are you sure?",
      text: "Note will be moved to trash !",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, do it!",
    }).then((result) => {
      if (result.value) {
        var token = localStorage.getItem("jwt");

        var config = {
          headers: { authorization: token },
        };
        setisloading(true);

        var payload = {
          noteid: selectednoteid,
          state: false,
        };

        console.log(payload);

        axios
          .post("/api/changestate", payload, config)
          .then((result) => {
            console.log(result.data);
            setisloading(false);
            closeModel();

            settodos((pre) => {
              var noteindex = pre
                .map(function (e) {
                  return e.id;
                })
                .indexOf(selectednoteid);
              console.log(noteindex);
              pre.splice(noteindex, 1);

              return [...pre];
            });

            Swal.fire("Deleted!", "Your note is moved to trash.", "success");
          })
          .catch((err) => {
            console.log(err);
          });
      }
    });
  };

  return (
    <div className="darkmode ">
      <Navbar username={username} />
      <div className="container ">
        <div className="container">
          <div className="columns is-centered">
            <div className="column is-half newnotediv">
              {" "}
              <form onSubmit={handleSubmit(addTodo)}>
                <textarea
                  id="textareanewnote"
                  className={
                    errors.todo
                      ? "textarea inputbordercolor newnotediv darkmode is-danger"
                      : "textarea inputbordercolor newnotediv darkmode"
                  }
                  placeholder="What's on your mind"
                  rows="1"
                  ref={register}
                  // value={newtodocontent}
                  // onChange={(e) => {
                  //   setnewtodocontent(e.target.value);
                  // }}
                  name="todo"
                ></textarea>
                {/* {errors.todo && <p> type something dude </p>} */}
                <div className="has-text-right">
                  <button
                    className="button is-info addnotebtn  is-small"
                    type="submit"
                    disabled={isSubmitting || errors.todo}
                    // onClick={console.log('sas ')}
                  >
                    Add
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>

        {isloading && (
          <progress className="progress is-success loadingbar " max="100">
            Loading...
          </progress>
        )}

        <div className="">
          {todos.map((ele, index) => {
            return (
              <div className="tile is-ancestor  ">
                {/* <div className="tile is-vertical is-8 is-centered"> */}
                <div className="tile">
                  <div className="tile is-parent is-vertical notetileouter  ">
                    <article
                      onClick={() =>
                        openModel(
                          ele.id,
                          ele.title,
                          ele.note,
                          ele.created,
                          ele.updated
                        )
                      }
                      className="tile is-child notification is-primary notetile"
                    >
                      <p className=" is-pulled-right">
                        Created : {moments(ele.created).fromNow()}
                      </p>
                      <br></br>
                      <p className="is-pulled-right ">
                        Updated : {moments(ele.updated).fromNow()}
                      </p>

                      <p className="title ">
                        {ele.encrypted ? "encrypted" : ele.title}
                      </p>
                      <p className="subtitle contentnote">
                        {ele.encrypted ? "encrypted" : ele.note}
                      </p>
                    </article>
                  </div>
                </div>{" "}
                {/* <textarea
                  // placeholder="What's next Lord Rizky "
                  class="form-control"
                  id="textareaaddtodo"
                  rows="3"
                  wrap="off"
                  // ref={register}
                  // name="todo"
                  onChange={(e) => {
                    onedit(index, e.target.value, ele._id);
                  }}
                  value={ele.encrypted ? "encrypted" : ele.content}
                ></textarea> */}
                {/* {errors.todo && <p> type something dude </p>} */}
              </div>
            );
          })}
        </div>

        <div className={modelopen ? "modal  is-active" : "modal "}>
          <div className="modal-background" onClick={() => closeModel()}></div>

          <div className="modal-card ">
            <header className="modal-card-head editnoteheader darkmode">
              <p className="modal-card-title">
                <input
                  className="input inputbordercolor notetitlechangeinput darkmode"
                  type="text"
                  value={selectednotetitle}
                  placeholder="Title"
                  onChange={(e) =>
                    editnote(e.target.value, selectednotecontent)
                  }
                ></input>
              </p>
              <button
                className="delete"
                onClick={() => closeModel()}
                aria-label="close"
              ></button>
            </header>{" "}
            <section className="modal-card-body darkmode contentnote">
              <textarea
                className="textarea darkmode inputbordercolor inputbordercolor"
                value={selectednotecontent}
                onChange={(e) => editnote(selectednotetitle, e.target.value)}
                rows={String(selectednotecontent.split("\n").length + 10)}
                placeholder="lets write something lord"
              ></textarea>
            </section>
            <footer className="modal-card-foot darkmode editnotefooter cardnotefooter">
              <div>
                <p className=" timeinmodal ">
                  Created : {moments(selectednotecreated).fromNow()}
                </p>
                <p className="timeinmodal  ">
                  Updated : {moments(selectednoteupdated).fromNow()}
                </p>{" "}
              </div>

              <button
                onClick={() => deactivatenoe()}
                className="deletenotebutton button is-small is-danger "
              >
                Delete
              </button>
            </footer>
          </div>
        </div>

        {todos.length === 0 ? "No Todos to show , Start with new one eh" : ""}
        {/* </div> */}
        {/* </div> */}
        <ToastContainer
          position="bottom-left"
          autoClose={2000}
          hideProgressBar={false}
          newestOnTop={false}
          closeOnClick={false}
          rtl={false}
          pauseOnFocusLoss
          draggable
          pauseOnHover
          // bodyStyle={{ backgroundColor: "#5bcd04" , colo}}
        />
      </div>
    </div>
  );
};

export default Dashboard;
