import React, { Component } from "react";
// import { Redirect, Link } from "react-router-dom";
import Navbar from "../../components/navbar/navbar";
import Alert from "../../components/alert/alert";
import "./login.scoped.css";
import axios from "axios";

import Footer from "../../components/footer/footer";

// const API = "http://localhost:8080";

const API = "https://gonotesrizky.herokuapp.com";

class Login extends Component {
  state = {
    token: "",
    // email: "",
    // password: "",
    altertext: "",
    altertshow: true,
    alertstyle: "",
  };

  changehandleremail = (event) => {
    this.setState({
      email: event.target.value,
    });
  };

  changehandlerpass = (event) => {
    this.setState({
      password: event.target.value,
    });
  };

  btn1handler = (e) => {
    e.preventDefault();

    this.setState({
      altertext: "",
      altertshow: true,
      alertstyle: "danger",
    });
    console.log("cliking");

    console.log("sending..............");
    console.log(this.state.email + this.state.password);

    axios
      .post(`${API}/login`, {
        email: this.state.email,
        password: this.state.password,
      })
      .then((data) => {
        console.log("awe mewwa - - -popopopopo");
        console.log(data);
        var body = data.data;

        if (body.msg === "success") {
          console.log("body - " + body);
          localStorage.setItem("jwt", body.token);

          this.props.history.push("/dashboard");
        }
      })
      .catch((err) => {
        //   M.toast({ html: "Invalid Credentials" });
        console.log(err.response.data.msg);

        if (err.response.data.msg === "Invalid_email") {
          this.setState({
            altertext: "Your are not in registerd aye!",
            altertshow: false,
            alertstyle: "danger",
          });
        }
        if (err.response.data.msg === "Invalid_password") {
          this.setState({
            altertext: "Are you sure these are your creds ? ",
            altertshow: false,
            alertstyle: "danger",
          });
        }
      });
  };

  render() {
    return (
      <div className=" is-desktop is-vcentered">
        <section className="hero  is-fullheight">
          <Navbar />
          <div className="hero-body">
            <div className="container">
              <Alert
                style={this.state.alertstyle}
                text={this.state.altertext}
                hidden={this.state.altertshow}
              />
              <div className="columns is-centered">
                <div className="column is-5-tablet is-4-desktop is-3-widescreen">
                  <form onSubmit={this.btn1handler} className="box">
                    <div className="field">
                      <label for="" className="label">
                        Email
                      </label>
                      <div className="control has-icons-right">
                        <input
                          id="email"
                          required
                          type="text"
                          name="email"
                          placeholder="Enter your email"
                          autofocus
                          onChange={this.changehandleremail}
                          className="input"
                        />
                        <span className="icon is-small is-right">
                          <i className="fa fa-envelope"></i>
                        </span>
                      </div>
                    </div>
                    <div className="field">
                      <label for="" className="label">
                        Password
                      </label>
                      <div className="control has-icons-right">
                        <input
                          className="input"
                          required
                          id="pass"
                          type="password"
                          name="pass"
                          placeholder="Enter your password"
                          onChange={this.changehandlerpass}
                        />
                        <span className="icon is-small is-right">
                          <i className="fa fa-lock"></i>
                        </span>
                      </div>
                    </div>

                    <div className="field">
                      <button type="submit" className="button is-success">
                        Login
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>{" "}
          <Footer />
        </section>
      </div>

      // <div className=" ">
      //   <Navbar />
      //   <div className="text-center">
      //     <div className="container">
      //       <form className="form-signin" onSubmit={this.btn1handler}>
      //         <img
      //           className="mb-4"
      //           src="https://img.icons8.com/nolan/64/000000/share.png"
      //           alt=""
      //           width={72}
      //           height={72}
      //         />
      //         <h1 className="h3 mb-3 font-weight-normal">Login to continue</h1>

      //         <label htmlFor="inputPassword" className="sr-only">
      //           Password
      //         </label>
      //         <input

      //         />
      //         <div className=" text-left mb-3">
      //           <a href="/forgotpassword">forgot password</a>
      //         </div>
      //         <button
      //           className="btn btn-lg btn-primary btn-block"
      //           type="submit"
      //         >
      //           Login
      //         </button>
      //         {/* <p className="mt-5 mb-3 text-muted ">Â© 2017-2019</p> */}
      //       </form>
      //       {this.state.creaderror && (
      //         <div className="container">
      //           <div className="credeer">Invalid Creadentials</div>
      //         </div>
      //       )}
      //     </div>
      //   </div>
      //   <Footer />
      // </div>
    );
  }
}

export default Login;
