import React, { Component } from "react";
import { withRouter } from "react-router-dom";
// import "./navbar.scoped.css";
// import "./responsive.scoped.css";
class Navbar extends Component {
  render() {
    return (
      <nav
        className="navbar is-dark"
        role="navigation"
        aria-label="main navigation"
      >
        <div className="navbar-brand">
          <a className="navbar-item" href="https://bulma.io">
            <img
              alt="HIDEOUT logo"
              src="https://img.icons8.com/wired/32/5bcd04/note.png"
              width="32"
              height="32"
            />
            HIDEOUT
          </a>

          <a
            href="# "
            role="button"
            className="navbar-burger burger"
            aria-label="menu"
            aria-expanded="false"
            data-target="navbarBasicExample"
          >
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
          </a>
        </div>

        {this.props.username ? (
          <div id="navbarBasicExample" className="navbar-menu ">
            <div className="navbar-end">
              <div className="navbar-item has-dropdown darkmode is-hoverable">
                <a href="# " className="navbar-item  has-text-white">
                  {this.props.username}
                </a>
                <div className="navbar-dropdown darkmode is-right">
                  <a href="# " className="navbar-item darkmode">
                    Trash
                  </a>
                  <hr className="navbar-divider " />
                  <a
                    href="# "
                    onClick={() => {
                      localStorage.removeItem("jwt");
                      this.props.history.push("/");
                    }}
                    className="navbar-item darkmode"
                  >
                    Logout
                  </a>
                  <div className="navbar-item darkmode">Version 0.8.2</div>
                </div>
              </div>{" "}
            </div>
          </div>
        ) : null}
      </nav>

      /* <nav className="navbar navbar-expand-lg fixed-top navbar-light bg-light">
        <div className="container">
       
          <div className="navbar-header">
            <div>
              <a href="/" className="navbar-brand">
                <img
                  src="https://img.icons8.com/nolan/64/000000/share.png"
                  alt=""
                />
              </a>
            </div>
            <div>
              <button
                className="navbar-toggler menuicon"
                type="button"
                data-toggle="collapse"
                data-target="#main-navbar"
                aria-controls="main-navbar"
                aria-expanded="false"
                aria-label="Toggle navigation"
              >
                <img src="https://img.icons8.com/material-sharp/24/000000/squared-menu.png"></img>
        
              </button>
            </div>
          </div>



          <div className="collapse navbar-collapse" id="main-navbar">
            <ul className="navbar-nav mr-auto w-100 justify-content-left ">
              <li className="nav-item ">
                <a className="nav-link" href="/login">
                  Login
                </a>
              </li>
              <li className="nav-item">
                <a className="nav-link" href="/signup">
                  Signup
                </a>
              </li>
              <li className="nav-item">
                <a className="nav-link" href="/guestlogin">
                  Guest
                </a>
              </li>
         
            </ul>
          </div>
        </div>
      </nav> */
    );
  }
}

export default withRouter(Navbar);

